<style>
  /* 页面元素初始化和常用样式定义-start *//*======== 全局 ========*/body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,p,blockquote,th,td {margin:0;padding:0;}
