import Btn1 from './Btn1/index.js';
import Back from './Back/index.js';
import Box1 from './Box1/index.js';
import Box2 from './Box2/index.js';
import Box3 from './Box3/index.js';
import Title1 from './Title/index.js';
import Modal from './Modal/index.js';


const components = [
  Btn1,
  Back,
  Box1,Box2,Box3,
  Title1,
  Modal
];

const install = function(Vue) {
  if (install.installed) return;
  components.map(component => Vue.component(component.name, component))
};

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install,
  Btn1,
  Back,
  Box1,Box2,Box3,
  Title1,
  Modal
}



