<template>
  <div class="Box3">
    <img class="lefttop" src="../../assets/lefttop.png"/>
    <img class="rightbottom" src="../../assets/rightbottom.png" />
    <img class="topright" v-show="hideLeft==true" src="../../assets/topright.png" />
    <img class="bottomleft" v-show="hideLeft==true" src="../../assets/bottomleft.png"/>
    <div class="col-md-12" style="height:100%;">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Box3',
    data () {
      return {
      }
    },
    props:{
      hideLeft:{
        default: true
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .Box3{position: relative;border: 1px solid #247bff;}
  .lefttop,.rightbottom,.bottomleft,.topright{position: absolute;width: 18px;z-index: 999999999999}
  .lefttop{left: 0;top: 0;}
  .rightbottom{right: 0;bottom: 0;}
  .bottomleft{bottom: 0;left: 0}
  .topright{top:0;right: 0}
</style>



