<template>
  <div class="Box2">
    <img class="leftbottom" src="../../assets/leftbottom.png" alt="">
    <img class="righttop" src="../../assets/righttop.png" alt="">
    <ul class="inforUl"  ref="back">
      <slot></slot>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Box2',
  data () {
    return {

    }
  },
  mounted(){
    this.$refs.back.style.background = this.backcolor
  },
  props:{
    backcolor:{
      default:"rgba(3,32,66,0.7)"
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .Box2{position: relative;padding: 1vh 1vh 1.5vh ;}
  .inforUl{border-radius: 5px;border:1px solid #247bff;height:100%;overflow: hidden;padding: 1.5vh;}
  ul{list-style: none;text-align: left}
  li{padding:5px;}
  li span{color:#fff}
  .leftbottom,.righttop{position: absolute;width: 13vh;z-index: 999999}
  .leftbottom{bottom: 0;left: 0;}
  .righttop{right: 0;top:0}

  /*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
  ::-webkit-scrollbar
  {
    width: 10px;
    height: 1vh;
    background-color:rgba(0,185,234,0);
  }

  /*定义滚动条轨道*/
  ::-webkit-scrollbar-track
  {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-left: 1px #247bff solid;
  }

  /*定义滑块*/
  ::-webkit-scrollbar-thumb
  {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color:rgba(0,179,234,0.5);
  }
</style>



