import Vue from 'vue'
import Router from 'vue-router'
import home from '../pages/home'
import api from '../pages/api'


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/api',
      name: 'api',
      component: api,
      children: [
        {
          path: '/',
          name: 'introduction',
          component: r => require.ensure([], () => r(require('../docs/introduction.md')))
        },
        {
          path: '/introduction',
          name: 'introduction',
          component: r => require.ensure([], () => r(require('../docs/introduction.md')))
        },
        {
          path: '/install',
          name: 'install',
          component: r => require.ensure([], () => r(require('../docs/install.md')))
        },
        {
          path: '/quickstart',
          name: 'quickstart',
          component: r => require.ensure([], () => r(require('../docs/quickstart.md')))
        },
        {
          path: '/changelog',
          name: 'changelog',
          component: r => require.ensure([], () => r(require('../docs/changelog.md')))
        },
        {
          path: '/back',
          name: 'back',
          component: r => require.ensure([], () => r(require('../docs/back.md')))
        },
        {
          path: '/title',
          name: 'title',
          component: r => require.ensure([], () => r(require('../docs/title.md')))
        },
        {
          path: '/box',
          name: 'box',
          component: r => require.ensure([], () => r(require('../docs/box.md')))
        },
        {
          path: '/button',
          name: 'button',
          component: r => require.ensure([], () => r(require('../docs/button.md')))
        },
        {
          path: '/tree',
          name: 'tree',
          component: r => require.ensure([], () => r(require('../docs/tree.md')))
        },
        {
          path: '/box',
          name: 'box',
          component: r => require.ensure([], () => r(require('../docs/button.md')))
        },
        {
          path: '/modal',
          name: 'modal',
          component: r => require.ensure([], () => r(require('../docs/modal.md')))
        }
      ]
    },

  ]
})



