<template>
  <div class="Btn1">
    <img class="bottomleft" src="../../assets/bottomleft.png"/>
    <img class="lefttop" src="../../assets/lefttop.png"/>
    <img class="rightbottom" src="../../assets/rightbottom.png" />
    <img class="topright" src="../../assets/topright.png" />
    <a :href="url">
      <i class=""></i>
      {{text}}
    </a>
  </div>
</template>

<script>
  export default {
    name: 'Btn1',
    data () {
      return {

      }
    },
    props:{
      text:'',
      url:{default:'#'}
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .Btn1{font-size: 15px;background: rgba(6,89,119,0.9);position: relative;cursor:pointer;padding:8px 0;display:inline-block}
  .Btn1 img{width:10px;position: absolute;}
  .Btn1 a{color:#ccc;padding:8px 10px;}
  .Btn1:hover a{color:#fff;}
  .bottomleft{bottom: 0;left: 0;}
  .topright{top:0;right: 0;cursor:pointer }
  .lefttop{left: 0;top:0;}
  .rightbottom{right:0;bottom: 0;}
  .Btn1:hover .bottomleft{bottom: -3px;left:  -3px;}
  .Btn1:hover .topright{top: -3px;right: -3px;cursor:pointer }
  .Btn1:hover .lefttop{left: -3px;top: -3px;}
  .Btn1:hover .rightbottom{right: -3px;bottom: -3px;}
</style>



