<template>
  <div class="Model">
    <div class="Modelbox">
      <img class="bottomleft" src="../../assets/bottomleft.png"/>
      <img class="lefttop" src="../../assets/lefttop.png"/>
      <img class="rightbottom" src="../../assets/rightbottom.png" />
      <img class="topright" src="../../assets/close.png" @click="Model"/>
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Model',
    data () {
      return {

      }
    },
    methods: {
      Model() {
        this.$emit('onModel',false);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .Model{width: 100%;height: 100%;background: rgba(0,0,0,0.4);position: absolute;left:0;top:0;padding:20px;}
  .Modelbox{border:1px solid #247bff;width:60vw;height:60vh;position:absolute;left: 50%;top:50%;margin:-30vh auto auto -30vw;background: rgba(6,89,119,0.9);overflow: hidden}

  .Modelbox>img{width:30px;position: absolute;}

  .bottomleft{bottom: 0;left: 0;}
  .topright{top:0;right: 0;cursor:pointer }
  .topright:hover{background:rgba(0,0,0,0.5);}
  .lefttop{left: 0;top:0;}
  .rightbottom{right:0;bottom: 0;}
</style>



